/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  min-width: 300px;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: hsl(0deg, 0%, 7%);
}

.basic-container {
  width: min(95%, 1440px);
  margin-inline: auto;
}

.T1 {
  font-size: clamp(2.25rem, 5vw, 2.625rem);
  line-height: clamp(120%, 2.5vw, 150%);
  margin: 0;
  margin-bottom: 0.62em;
}

.fw-normal {
  font-weight: 300;
}

.fw-bolder {
  font-weight: 600;
}

.fw-bold {
  font-weight: 700;
}

.button {
  --color-base: hsl(21deg, 50%, 79%);
  --color-hover: hsl(21deg, 50%, 90%);
  --color-active: hsl(21deg, 20%, 60%);
  --color-text: hsl(0deg, 100%, 100%);
  font-size: 1rem;
  padding: 1em 2.5em;
  border: none;
  border-radius: 2em;
  font-weight: 700;
  color: var(--color-text);
  background-color: var(--color-base);
  cursor: pointer;
  transition-property: background-color;
  transition-duration: 200ms;
  transition-timing-function: ease-in-out;
}
.button:focus-visible {
  background-color: var(--color-hover);
}
@media (hover: hover) {
  .button:hover {
    background-color: var(--color-hover);
  }
}
.button:active {
  transition-property: background-color;
  transition-duration: 0s;
  background-color: var(--color-active);
}

.button--warning {
  --color-base: hsl(354deg, 81%, 56%);
  --color-hover: hsl(354deg, 81%, 80%);
  --color-active: hsl(354deg, 81%, 40%);
}

.close-button {
  border: none;
  padding: 0;
  height: 1em;
  width: 1em;
  background: transparent url("data:image/svg+xml,%3Csvg viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18 6L6 18' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M6 6L18 18' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") center/1em auto no-repeat;
  cursor: pointer;
}
.close-button:focus-visible {
  opacity: 0.5;
}
@media (hover: hover) {
  .close-button:hover {
    opacity: 0.5;
  }
}
.close-button:active {
  opacity: 1;
}

.task-module {
  margin-block: 6rem;
}

@media (min-width: 576px) {
  .task-module {
    margin-inline: auto;
    width: fit-content;
    display: flex;
    transition: gap 0.5s;
    column-gap: max(20px, 4vw);
  }
  .task-module > * {
    flex: 1 1 0;
  }
}
@media (min-width: 768px) {
  .task-module {
    column-gap: min(6.6vw, 95px);
  }
}
.task-module__image {
  margin-bottom: 1rem;
}

.task-module__image img {
  width: 100%;
  aspect-ratio: 4/3;
  object-fit: cover;
  max-height: inherit;
  max-width: inherit;
}

@media (min-width: 576px) {
  .task-module__image {
    max-height: 330px;
    max-width: 330px;
    margin-bottom: 0;
  }

  .task-module__image img {
    aspect-ratio: 1/1;
  }
}
@media (min-width: 992px) {
  .task-module__image {
    flex: initial;
    max-width: 505px;
    max-height: 330px;
    width: 100%;
  }

  .task-module__image img {
    aspect-ratio: initial;
  }
}
.task-module__text-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.task-module__text-container p {
  margin-bottom: clamp(1.5rem, 3vw, 2rem);
}

@media (min-width: 576px) {
  .task-module__text-container {
    max-width: 380px;
  }

  .task-module__text-container .button {
    width: min-content;
  }
}
.modal {
  position: fixed;
  inset: 0;
  background-color: rgba(18, 18, 18, 0.5);
  display: none;
  min-width: 300px;
  transition: opacity 0.15s linear;
}

.modal:not(.show) {
  opacity: 0;
}

.modal__dialog {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  transition: transform 0.3s linear;
}

.modal:not(.show) .modal__dialog {
  transform: translateY(-100%);
}

.modal__close-btn {
  position: absolute;
  right: 32px;
  top: 32px;
  font-size: 28px;
}

.modal__content {
  background-color: white;
  width: min(80%, 920px);
  position: relative;
  padding-inline: clamp(30px, 10vw, 95px);
  padding-block: clamp(50px, 10vw, 56px);
  max-height: 95vh;
  display: flex;
  flex-direction: column;
}

.modal__footer {
  display: flex;
  flex-direction: column;
  margin-bottom: -1.5rem;
  min-height: 0;
}

.modal__text {
  margin-bottom: 1.5rem;
}

.modal__buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.modal__buttons > .button:last-child {
  margin-bottom: 1.5rem;
}

@media (min-width: 576px) {
  .modal__buttons {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: baseline;
  }
}
.d-none {
  display: none;
}

.stop-scroll {
  height: 100vh;
  overflow: hidden;
}

.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}

.loader {
  margin: 60px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(245, 163, 171, 0.2);
  border-right: 1.1em solid rgba(245, 163, 171, 0.2);
  border-bottom: 1.1em solid rgba(245, 163, 171, 0.2);
  border-left: 1.1em solid hsl(354deg, 81%, 80%);
  transform: translateZ(0);
  animation: load8 1.1s infinite linear;
}

@-webkit-keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.user-table {
  min-height: 0;
  overflow: auto;
  border-block: 2px solid black;
  display: flex;
  justify-content: center;
  margin-bottom: 1.5rem;
}

.user-table > .button {
  margin-block: 1rem;
}